<template>
  <div id="app">
    <el-container>
      <el-header>
        <Header></Header>
      </el-header>
      <el-main :style="defaultHeight">
        <router-view></router-view>
      </el-main>
      <el-footer>
        <Footer></Footer>
      </el-footer>
    </el-container>
  </div>
</template>

<script>
import Header from './components/header'
import Footer from './components/footer'

export default {
  name: 'app',
  components: {
    Header,
    Footer
  },
  data(){
    return {
      defaultHeight: {
        height: ''
      }
    }
  },
  methods: {
    getHeight() {
      this.defaultHeight.height = window.innerHeight - 120 + 'px';
    }
  },
  created() {
    window.addEventListener('resize', this.getHeight());
    this.getHeight();
  }
}
</script>

<style>
#app {
  font-family: "Ubuntu","Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
}
.intro-detail {
  background-color: #F0FFFF;
  padding: 2em;
  border-radius: 8px;
  border-style: solid;
  border-width: 1px;
  border-color: rgba(0, 0, 0, 0.1);
  color: #00C5CD; 
  margin-bottom: 1em;
  font-size: 0.9em;
}
</style>
